import React from "react";
import './about1.scss';
// import Chart2 from "./Chart2";
import { ResponsiveRadar } from '@nivo/radar'
// import Chart from './Chart';

const data = [
    {
      "taste": "HTML",
      "level": 95
    },
    {
      "taste": "CSS & SCSS",
      "level": 95
    },
    {
      "taste": "Javascript",
      "level": 90
    },
    {
      "taste": "nodejs",
      "level": 60
    },
    {
      "taste": "React",
      "level": 75
    },
    {
      "taste": "Git",
      "level": 80
      }
  ]

function About1(props){
    return(
            <div className={props.onoff}>
                <div className="about1">
                    <div className="slogun">
                        "To be&nbsp;
                        <div className="letterbox">
                            <div className="text1">special</div>
                            <div className="text2">smart</div>
                            <div className="text3">creative</div>                        
                        </div>
                        &nbsp;developer not your average developer"
                    </div>
                    <div className="picturebox">
                        <div className="picture"></div>
                    </div>
                    <div className="record">
                        <h2>김민준</h2>
                        <h2>1994.02.24 (29세)</h2>
                        <h2>인천광역시 남동구 구월동</h2>

                    </div>
                    <div className="chart">
                    <ResponsiveRadar
        data={data}
        keys={[ 'level' ]}
        indexBy="taste"
        margin={{ top: 50, right: 80, bottom: 50, left: 80 }}
        borderWidth={0}
        borderColor={{ from: 'color', modifiers: [] }}
        gridShape="linear"
        gridLabelOffset={22}
        dotSize={10}
        dotColor={{ theme: 'background' }}
        dotBorderWidth={3}
        enableDotLabel={false}
        dotLabelYOffset={-7}
        colors={{ scheme: 'nivo' }}
        fillOpacity={0.35}
        blendMode="normal"
        motionConfig={{
            mass: 1,
            tension: 136,
            friction: 26,
            clamp: true,
            precision: 0.01,
            velocity: 0
        }}
        theme={{
            "background": "#1d2630",
            'textColor':'#bbc4b9',
            'fontSize':14,
            'legend':{
                'title':{
                    'text':{
                        'fontSize':10,
                        'fill':'#ffffff'
                    }
                },
                'text':{
                    'fontSize':20,
                    'fill':'#ffffff'
                }
            }

            }
            }
        legends={[
            {
                anchor: 'top-left',
                direction: 'column',
                translateX: -30,
                translateY: 0,
                itemWidth: 80,
                itemHeight: 20,
                itemTextColor: '#bbc4b9',
                symbolSize: 16,
                symbolShape: 'circle',
                effects: [
                    {
                        on: 'hover',
                        style: {
                            itemTextColor: '#d48282'
                        }
                    }
                ]
            }
        ]}
    />
                    </div>
                    <div className="introduce">
                        <h2><b>2021.09-2022.02</b> 라인컴퓨터아트학원 웹퍼블리셔 과정 수료</h2>
                        <h2><b>2019.09-2021.06</b> 호리바코리아 근무</h2>
                        <h2><b>2013.03-2020.03</b> 인천대학교 기계공학과 졸업</h2>
                    </div>
                </div>
            </div>    
    )
};

export default About1;