import React from "react";
import { useState } from 'react';
import './contact.scss';
import Particles from "react-tsparticles";
import { ImPhone } from "react-icons/im";
import { ImInstagram } from "react-icons/im";
import { ImMail2 } from "react-icons/im";
import { ImGithub } from "react-icons/im";

function Contact(props){
    const [color1, setColor1]=useState('#bbc4b9');
    const particlesInit = (main) => {
        console.log(main);
    
        // you can initialize the tsParticles instance (main) here, adding custom shapes or presets
    };
    
    const particlesLoaded = (container) => {
        console.log(container);
    };
    
    return(
        <div className={props.onoff}>
            <Particles
    id="tsparticles"
    init={particlesInit}
    loaded={particlesLoaded}
    options={{
      background: {
        color: {
          value: "#1d2630",
        },
      },
      fpsLimit: 120,
      interactivity: {
        events: {
          onClick: {
            enable: true,
            mode: "push",
          },
          onHover: {
            enable: true,
            mode: "repulse",
          },
          resize: true,
        },
        modes: {
          bubble: {
            distance: 400,
            duration: 2,
            opacity: 0.8,
            size: 40,
          },
          push: {
            quantity: 4,
          },
          repulse: {
            distance: 200,
            duration: 0.4,
          },
        },
      },
      particles: {
        color: {
          value: "#ffffff",
        },
        links: {
          color: "#ffffff",
          distance: 150,
          enable: true,
          opacity: 0.5,
          width: 1,
        },
        collisions: {
          enable: true,
        },
        move: {
          direction: "none",
          enable: true,
          outMode: "bounce",
          random: false,
          speed: 1,
          straight: false,
        },
        number: {
          density: {
            enable: true,
            area: 800,
          },
          value: 80,
        },
        opacity: {
          value: 0.5,
        },
        shape: {
          type: "circle",
        },
        size: {
          random: true,
          value: 5,
        },
      },
      detectRetina: true,
    }}
  />
            <div className="contactbox">
                <h1>CONTACT</h1>
                <div className="info">
                    <div className="name">김민준</div>
                    <div className="phone"><ImPhone/>&nbsp;010-3367-7126</div>
                    <div className="mail"><ImMail2 />&nbsp;m6827126@gmail.com</div>
                    <div className="insta1"><ImInstagram />&nbsp;https://www.instagram.com/devel_jun/</div>
                    <div className='git'><ImGithub />&nbsp;https://github.com/MinJunKimsdaads</div> 
                </div>     
            </div>
        </div>
    )
};

export default Contact;