import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import './work.scss';
import About1 from '../../about/About1';
import Contact from '../../contact/Contact';
import Detailtitle from './Detailtitle';
import Btnmain from './Btnmain';
import Btnsub from './Btnsub';
import Des from './Des';





function Work(props) {
  const [detail, setDetail]=useState(false);
  const [detail1, setDetail1]=useState(true);

	const [workonoff, setWorkonoff]=useState('slideshowon');
  const [aboutonoff, setAboutonoff ]=useState('aboutboxoff');
  const [contactonoff, setContactonoff]=useState('contactoff');
  
  const portfolTitle1=[
	  'SAMSUNG',
	  'PLAYTOO',
	  'G-SHOCK',
	  'SEOULTOUR',
	  'Investing Flex',
	  'Gyeongbokung',
	  'Osulloc',
	  'TESLA',
	  'Weather Today'
  ]
  const [porfolTitle, setPofolTitle]=useState('');

  const class2=[
	'content-carrousel1',
	'content-carrousel2',
	'content-carrousel3',
	'content-carrousel4',
	'content-carrousel5',
	'content-carrousel6',
	'content-carrousel7',
	'content-carrousel8',
	'content-carrousel9',
  ]
  const [class1, setClass1]=useState('content-carrousel');

  const [width1, setWidth1]=useState('300px');
  const [height1, setHeight1]=useState('200px');
  const bgcolor=[
	  '#ece8dd',
	  '#dfe9f3',
	  '#0c0c0c',
	  '#f5efdf',
	  '#31447f',
	  '#8ba2a7',
	  '#d5d5d5',
	  '#090911',
	  '#dbe5e2',
  ]

  const [bgcolor1, setBgcolor1]=useState('#1d2630');

  const color1=[
	'#55729c',
	'#ae75e6',
	'#da452f',
	'#be998f',
	'#d2ddf3',
	'#e8e7c8',
	'#53bb8a',
	'#f0f0f0',
	'#00a8a9'
  ]
  const [color, setColor]=useState('');

  const des = [
	'경쟁사인 애플과 비교하여 부족한 웹사이트 스크롤 기능을 추가하여 역동성을 추가하였습니다.(1Week)',
	'ES6를 공부하고자 강의를 보면서 게임을 제작해보았습니다.(1Week)',
	'맨 처음 시작한 프로젝트이며 저가의 G-SHOCK을 고급스럽게 리뉴얼하였습니다.(2Weeks)',
	'5개의 주제와 한글만을 사용한 컨셉츄얼한 서울 관광 홈페이지를 제작하였습니다.(1Week)',
	'주식 API를 이용하여 웹과 어플리케이션의 특성을 가질 수 있는 반응형 웹을 제작해보았습니다.(2Weeks)',
	'팀 프로젝트로써 조장이 되어 경복궁 홍보페이지의 메인페이지를 제작하였습니다.(2Weeks)',
	'일본 웹 스타일을 반영하여 새로 텍스트와 가로 스크롤을 이용하여 페이지를 제작하였습니다.(2Weeks)',
	'3D파일도 렌더링이 가능할 까라는 호기심으로 제작한 TESLA model S 홍보페이지 입니다.(2Weeks)',
	'api를 이용하여 지역별 날씨, 시간과 날씨에 따른 배경, 날씨 예보 페이지를 제작하였습니다.(1Week)',
  ]
  const [des1, setDes1]=useState('');

  const lang=[
	'#HTML5 #SCSS #Vanilla js #GSAP #ScrollMasic #Responsive Web',
	'#HTML5 #CSS3 #Vanilla js #nodejs',
	'#HTML5 #CSS3 #Vanilla js',
	'#HTML5 #CSS3 #Vanilla js #Responsive Web',
	'#HTML5 #CSS3 #Vanilla js #nodejs #Responsive Web',
	'#HTML5 #CSS3 #Vanilla js #ScrollMasic #GSAP',
	'#HTML5 #CSS3 #Vanilla js',
	'#HTML5 #SCSS #Vanilla js #Threejs',
	'#HTML5 #CSS3 #Vanilla js',
  ]
  const [lang1, setLang1]=useState('');

	const mainurl1=[
		'https://minjunkimsdaads.github.io/Portfolio-4.-Samsung/',
		'https://webgamepf.herokuapp.com/',
		'https://minjunkimsdaads.github.io/Portfolio-1.G-SHOCK/',
		'https://minjunkimsdaads.github.io/Portfolio-2.Seoultour/',
		'https://nodekmj.herokuapp.com/',
		'https://minjunkimsdaads.github.io/Portfolio-3.-Gyeongbokung/',
		'https://minjunkimsdaads.github.io/portfolio_osulloc/',
		'https://minjunkimsdaads.github.io/3dmodel/',
		'https://minjunkimsdaads.github.io/weather/',
	]
  const [mainurl, setMainurl]=useState('');
	const suburl1=[
		'https://minjunkimsdaads.github.io/Portfolio-4.-Samsung/sub1.html',
		'https://webgamepf.herokuapp.com/main',
		'https://minjunkimsdaads.github.io/Portfolio-1.G-SHOCK/sub1.html',
		'https://minjunkimsdaads.github.io/Portfolio-2.Seoultour/seoultour_sub.html',
		'https://nodekmj.herokuapp.com/main',
		'https://minjunkimsdaads.github.io/Portfolio-3.-Gyeongbokung/sub2.html',
		'https://minjunkimsdaads.github.io/portfolio_osulloc/sub.html',
		'https://minjunkimsdaads.github.io/3dmodel/',
		'https://minjunkimsdaads.github.io/weather/apps/leaflet-owm.html',
	]
  const [suburl, setSuburl]=useState('');

  const [number, setNumber]=useState('');
  

  useEffect(()=>{
	setColor(color1[number]);
	setBgcolor1(bgcolor[number]);
	setDes1(des[number]);
	setLang1(lang[number]);
	setPofolTitle(portfolTitle1[number]);
	setClass1(class2[number]);
	setMainurl(mainurl1[number])
	setSuburl(suburl1[number])
  },[number])

  useEffect(()=>{
	// setBgcolor1(bgcolor[number]);
	setClass1('content-carrousel');
  },[])

  return(
		<>
		<div className="header_box" style={{color:color}}>
      <div className="header_title">
        KIMMINJUN
      </div>
      <div className="header_index">
        <div className="work"onClick={()=>{
          setWorkonoff('slideshowon');
          setAboutonoff('aboutboxoff');
          setContactonoff('contactoff');
					setDetail(false);
					setDetail1(true);
					setClass1('content-carrousel')
					setWidth1('300px');
					setHeight1('200px');
					setColor('#bbc4b9');
					setBgcolor1('#1d2630');
        }}>WORK</div>
        <div className="about" onClick={()=>{
          setWorkonoff('slideshowoff');
          setAboutonoff('aboutboxon');
          setContactonoff('contactoff');
					setDetail(false);
					setDetail1(true);
					setClass1('content-carrousel')
					setWidth1('300px');
					setHeight1('200px');
					setColor('#bbc4b9');
					setBgcolor1('#1d2630');
        }}>ABOUT</div>
        <div className="contact" onClick={()=>{
          setWorkonoff('slideshowoff');
          setAboutonoff('aboutboxoff');
          setContactonoff('contacton');
					setDetail1(true);
					setClass1('content-carrousel')
					setWidth1('300px');
					setHeight1('200px');
					setColor('#bbc4b9');
					setBgcolor1('#1d2630');
        }}>CONTACT</div>
      </div>
    </div>
		<section className={workonoff} style={{backgroundColor:bgcolor1}}>
			{detail ? <button className='inc' style={{color:color}} onClick={()=>{
				if(number+1>8){
					setNumber(0);
				}else{
					setNumber(number+1);
				}
			}}>Next</button>:null}
			{detail ? <button className='desc' style={{color:color}} onClick={()=>{
				if(number<=0){
					setNumber(8);
				}else{
					setNumber(number-1);
				}
			}}>Before</button>:null}
			{detail1 ? <div className='detail' style={{color:color}} onClick={()=>{
				setNumber(0);
				setDetail(true);
				setDetail1(false);
				setPofolTitle('SAMSUNG');
				setColor(color1[number]);
				setBgcolor1(bgcolor[number]);
				setClass1(class2[number]);
				setWidth1('350px');
				setHeight1('250px');
			}}>EXPLORE</div>:null}
		
			<div className="entire-content">
				<div className={class1}>
					<figure style={{width:width1, height:height1}}></figure>
					<figure style={{width:width1, height:height1}}></figure>
					<figure style={{width:width1, height:height1}}></figure>
					<figure style={{width:width1, height:height1}}></figure>
					<figure style={{width:width1, height:height1}}></figure>
					<figure style={{width:width1, height:height1}}></figure>
					<figure style={{width:width1, height:height1}}></figure>
					<figure style={{width:width1, height:height1}}></figure>
          <figure style={{width:width1, height:height1}} ></figure>
		    	</div>
	    	</div>
			{detail ? <Detailtitle title={porfolTitle} color1={color}/>:null}
			{detail ? <Des color1={color} des={des1} lang={lang1}/>:null}
			{detail ? <Btnmain color1={color} url={mainurl} />:null}
			{detail ? <Btnsub color1={color} url={suburl}/>:null}
			{detail ? <div className='quitbtn' style={{color:color}} onClick={()=>{
				setDetail(false);
				setDetail1(true);
				setClass1('content-carrousel')
				setWidth1('300px');
				setHeight1('200px');
				setColor('#bbc4b9');
				setBgcolor1('#1d2630');
			}}>Quit</div>:null}
 
    </section>
		<About1 onoff={aboutonoff}></About1>
    <Contact onoff={contactonoff}></Contact>
		<div className="footer_box" style={{color:color}}>
      <div className="footer_title">
        UI Developer / Frontend Developer
      </div>
      <div className="footer_index">
        <a href="mailto:m6827126@gmail.com" className="email">EMAIL</a>
        <a href="https://www.instagram.com/devel_jun/" className="insta">INSTAGRAM</a>
      </div>
    </div> 
		</>
  )
}

export default Work;